<template>
    <a-row>
        <a-col :xl="5" :lg="4" :md="3" :sm="2" :xs="1"></a-col>
        <a-col :xl="14" :lg="16" :md="18" :sm="20" :xs="22">
            <Step :current="_step"></Step>
            <div style="height: 10px"></div>
            <FormComponent v-if="_step===0"></FormComponent>
        </a-col>
        <a-col :xl="5" :lg="4" :md="3" :sm="2" :xs="1"></a-col>
    </a-row>
</template>

<script>
    import {getActivistUser} from '@/api/user'
    import {empty} from "@/libs/tools";
    import Step from './components/Steps'
    import FormComponent from './components/Form'

    export default {
        name: "Index",
        components: {Step, FormComponent},
        props: {
            activist_code: {
                required: true,
                type: [Number, String],
            },
            step: {
                required: true,
                type: [Number, String],
            },
        },
        computed: {
            _step() {
                return parseInt(this.step)
            },
        },
        methods: {
            init() {
                if (!empty(this.activist_code)) {
                    getActivistUser(this.activist_code)
                        .then(res => {
                            console.log(res)
                        })
                }
            }
        },
        watch: {
            $route: {
                handler: 'init',
                deep: true,
                immediate: true,
            }
        }
    }
</script>

